:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

body {
  margin: 12px;
  font-family: Arial, Helvetica, sans-serif;
}

b {
  /* margin: 0; */
  /* font-size: 1em; */
  text-align: center;
}

.gallery__info li {
  margin: 0 auto;
}

.info-item {
  margin: 0;
  text-align: center;
}

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: -12px -12px 0px -12px;
  height: 32px;
  background-color: #3f56b2;
}

.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 7px 10px;
  border: none;
  border-radius: 4px;
  color: white;
  background-color: #3f56b2;
  cursor: pointer;
}

.is-hidden {
  display: none;
}

.gallery {
  margin: 12px auto;
  display: grid;
  max-width: 1280px;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  grid-auto-rows: 220px;
  grid-gap: 12px;
  justify-content: center;
  padding: 0;
  list-style: none;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 80%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.photo-card {
  display: block;
  text-decoration: none;
  height: 100%;
  color: black;
}

.photo-card:hover {
  cursor: zoom-in;
}

.info {
  display: flex;
  justify-content: space-evenly;
  margin: 0;
  padding: 0;
  list-style: none;
}
